* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app-h1 {
  text-align: center;
  margin: 0rem 0rem;
}
.app-p {
  text-align: center;
  font-size: 1.2rem;
}
.app-players {
  width: 60%;
  height: 4rem;
  margin: 0 auto;
  display: flex;
}

.app-players-section {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 4rem;
  font-size: 1.2rem;
}

.app-board {
  width: 100%;
  height: 15rem;
  margin: 1rem auto 0rem;
  display: flex;
  flex-wrap: wrap;
}

.app-board-box {
  width: 33.33%;
  height: 5rem;
  border-right: 1px solid grey;
  border-bottom: 1px solid grey;
  font-size: 5rem;
  text-align: center;
  line-height: 5rem;
  text-transform: capitalize;
}
.app-board-box:hover {
  cursor: pointer;
}
.app-board-box-nb {
  width: 33.33%;
  height: 5rem;
  border-right: 0px solid grey;
  border-bottom: 1px solid grey;
  font-size: 5rem;
  text-align: center;
  line-height: 5rem;
}
.app-board-box-nbb {
  width: 33.33%;
  height: 5rem;
  border-right: 1px solid grey;
  border-bottom: 0px solid grey;
  font-size: 5rem;
  text-align: center;
  line-height: 5rem;
}
.app-board-box-nb-nbb {
  width: 33.33%;
  height: 5rem;
  border-right: 0px solid grey;
  border-bottom: 0px solid grey;
  font-size: 5rem;
  text-align: center;
  line-height: 5rem;
}

.app-win-lose {
  text-align: center;
  font-size: 2rem;
}
.app-win-lose-condition {
  text-align: center;
  font-size: 2rem;
  color: red;
  font-weight: bolder;
}

.won {
  color: red;
  font-weight: bolder;
}
